import React from "react";
import { createAvatar, Options } from "@dicebear/avatars";
import * as adventurer from "@dicebear/avatars-avataaars-sprites"; // Import kiu style adventurer t Dicebear
import {
  Eyebrow,
  Eyes,
  Mouth,
} from "@dicebear/avatars-avataaars-sprites/dist/options";
import { Box } from "@mui/material";

interface AvatarComponentProps {
  emotion: string;
}

interface OptionsMotion {
  eyes: Eyes;
  eyebrow: Eyebrow;
  mouth: Mouth;
}

function generateOptionsMotion(emotion: string): OptionsMotion {
  let optionsMotion: OptionsMotion;

  switch (emotion) {
    case "happy":
      optionsMotion = {
        eyes: ["happy"],
        eyebrow: ["raisedExcited"],
        mouth: ["smile"],
      };
      break;
    case "sad":
      optionsMotion = {
        eyes: ["cry"],
        eyebrow: ["sadConcerned"],
        mouth: ["sad"],
      };
      break;
    case "angry":
      optionsMotion = {
        eyes: ["squint"],
        eyebrow: ["angryNatural"],
        mouth: ["serious"],
      };
      break;
    case "surprised":
      optionsMotion = {
        eyes: ["surprised"],
        eyebrow: ["raisedExcitedNatural"],
        mouth: ["concerned"],
      };
      break;
    case "disgusted":
      optionsMotion = {
        eyes: ["xDizzy"],
        eyebrow: ["frownNatural"],
        mouth: ["vomit"],
      };
      break;
    case "fearful":
      optionsMotion = {
        eyes: ["side"],
        eyebrow: ["sadConcernedNatural"],
        mouth: ["scream"],
      };
      break;
    case "neutral":
    default:
      optionsMotion = {
        eyes: ["default"],
        eyebrow: ["defaultNatural"],
        mouth: ["serious"],
      };
      break;
  }

  return optionsMotion;
}

const AvatarComponent: React.FC<AvatarComponentProps> = ({ emotion }) => {
  const getAvatarSvg = (emotion: string) => {
    // const seed = emotion; // Dng emotion lm seed  to avatar nht qun
    let optionsMotion: OptionsMotion = generateOptionsMotion(emotion);
    let options: adventurer.Options & Options = {
      style: "transparent", // Kiu hin th, c th l "circle" hoc "transparent"
      mode: "include", // Bao gm cc thuc tnh mong mun
      top: ["shortFlat"], // Kiu tc ngn cho nam gii
      topChance: 100, // Xc sut xut hin tc
      hatColor: ["black"], // Mu m (nu c i m)
      hairColor: ["black"], // Mu tc en, ph bin  ngi chu 
      accessories: ["prescription01"], // eo knh n gin (ty chn)
      accessoriesChance: 0, // Xc sut eo ph kin,  50%  xut hin ngu nhin
      accessoriesColor: ["black"], // Mu knh
      facialHair: ["light"], // Ru ngn, nh (thng thy  nam gii chu )
      facialHairChance: 0, // T l xut hin ru
      facialHairColor: ["black"], // Mu ru
      clothes: ["blazerAndSweater"], // Trang phc o s mi
      clothesColor: ["gray02"], // Mu o
      ...optionsMotion,
      skin: ["light"], // Mu da vng c trng ca ngi chu 
      clotheGraphics: ["diamond"], // Hnh  ha trn o (ty chn)
      width: 1000,
      height: 1000,
    };

    return createAvatar(adventurer, options);
  };

  let svgHTML = getAvatarSvg(emotion);
  svgHTML = svgHTML.replace(
    /height="1000"/,
    'height="1000" style="max-width:100%;max-height:100%;"'
  );

  return (
    <Box
      overflow="hidden"
      height="100%"
      width="100%"
      dangerouslySetInnerHTML={{ __html: svgHTML }}
    ></Box>
  );
};

export default AvatarComponent;
